@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap);


body {
    font-family: 'Noto Sans TC', sans-serif !important;
    
}

.navbar-brand .brandName {
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    font-weight: 500;

    margin: auto;
}

.navbar-brand .brandImg {
    height: 3rem;
    margin: auto;
}


.cover {
    width: Calc(100%-2rem) ;
    height: 90vh;
    
    border-radius: 5px;
    background-color:#F0F0F0;

    margin: 1rem;

    position: relative;
}

.cover .title {
    font-size: 3rem;
    letter-spacing: 1rem;
    line-height: 4rem;
    font-weight: 600;

    padding-left: 5rem;

    
}

.cover .subtitle {
    font-size: 2rem;
    letter-spacing: 0.8rem;
    font-weight: 500;

    padding-top: 5rem;
    padding-left: 5rem;

    display: flex;
    margin: auto;
}

.cover .down {
    font-size: 3rem;

    

    position: absolute;
    bottom: 1rem;
    left: 5rem;
}

.slogan {
    font-size: 2rem;
    letter-spacing: 0.5rem;
    font-weight: 400;
    
    margin-left: 5rem;
}

.sloganBack {
    display:flex;
    align-items:center;
    justify-content:center;

    margin-left: auto;
    margin-right: 1rem;

    position: relative;
    top: -5rem;

    width: 80%;
    height: 20rem;

    border-radius: 0.5%;
    background-color: #46A3FF;


}

.footer {
    width: 100%;
    height: 20rem;

    display: block;
    background-color: black;
    
    color: blanchedalmond;

    margin-top: 5rem;
}

.homePage > .about{
    height: 100vh;

}

.homePage > .about .card{
    height: 90vh;
    width: 100%;

    background-color:#F0F0F0;
    border-radius: 5px;
    border: none;

    display: flex;

    
}


.homePage > .about .card .title {
    width: 100%;

    font-size: 3rem;
    letter-spacing: 0.3rem;
    font-weight: 500;

    text-align: center;
    
    line-height: 3.2rem;
}

.homePage > .about .card .subTitle {
    width: 100%;

    font-size: 1.5rem;
    letter-spacing: 0.1rem;
    font-weight: 400;

    text-align: center;

    
}

.homePage > .about .card .imagebox {
    width: 100%;
    height: 100%;
    
    

    display:flex;
    align-items:center;
    justify-content:center;

    
}

.homePage .joinUs {
    width: 100%;
    height: 50vh;

    background-color: #D0D0D0;

}

.topicSection {
    display:flex;
    align-items:center;
    justify-content:center;

    width: Calc(100%-2rem);
    height: 6rem;

    background-color: #D0D0D0;

    margin: 1rem;

    border-radius: 5px;
}

.topicSection .title{
    font-size: 2.5rem;
    letter-spacing: 0.3rem;
    font-weight: 400;

    
}

.publications-card {
    margin-top: 1rem;
    
    width: 100%;
    min-height: 15rem;

    transition: 0.25s;

    border-radius: 5px;
    /* border-color: black;
    border-style: solid;
    border-width: 1.1px; */

    background-color: transparent;
    color:black;
}

.publications-card:hover{
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    
    background-color: #D0D0D0;
    color:black;

    transition: 0.25s;
}

.publications-card > .title {
    font-size: 1.8rem;
    letter-spacing: default;
    font-weight: 500;
    line-height: normal;

    margin-top: 0rem;
    margin-bottom: 0px;

    padding: 0.5rem;
}

.publications-card > .author {
    font-size: 1rem;
    letter-spacing: default;
    font-weight: 400;
    line-height: normal;

    margin-top: 0rem;
    margin-bottom: 0px;

    padding: 0.5rem;
}
.publications-card > .at {
    font-size: 1.2rem;
    letter-spacing: default;
    font-weight: 400;
    line-height: normal;
    
    margin-top: 0rem;
    margin-bottom: 0px;

    padding: 0.5rem;
}
